/* Used by App.js, ExampleRetriever.js, Menu.js */

:root {
  --brand: #449ac2;
  --theme: #6f6259;
  --accent: #B0a96f;
  --light: #ffffff;
  --dark: #3c3c3c;
}


body {
  background-image: url('./resources/paper-small.jpeg');
}

/*ExampleRetriever*/
h1 {
  color: var(--light);
  text-align: center;
  margin: 0;
}

h2, h3, h4, h5, h6 {
  color: var(--dark);
  text-align: center;
  margin: 0;
  margin-bottom: 10px;
}

button {
  text-align: center;
  font-family: 'Poppins', 'Avenir', 'Helvetica', sans-serif;
  font-weight: 600;
  padding: 10px 30px;
  color: var(--light);
  background-color: var(--brand);
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #B0a96f;
  color: #ffffff;
}

.div-header{
  padding: 30px 5%;
  background-color: var(--theme);
  color: var(--light);
  margin: 0;
  margin-bottom: 3%;
  display: flex;
  justify-content: space-between;
}

.div-header button {
  background-color: var(--light);
  color: var(--dark);
}

.menu-buttons {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: row;
  min-height: 250px;
}

.div-vocab {
  margin-top: 10px;
  display: flex;
}

.finishedMessage {
  margin: auto;
  width: 50%;
  min-width: 400px;
}

.finishedMessage p {
  margin-top: 40px;
  margin-bottom: 40px;
}


.quiz {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.exampleBox {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
}

.spanishExample, .englishTranslation {
  width: 50%;
  min-width: 300px;
  min-height:200px;
  color: var(--dark);
  background-color: var(--light);
  text-align: center;
  display: flex;
  flex-direction: column;
  vertical-align: center;
  justify-content: center;
  padding: 25px 50px;
  border: 4px solid var(--accent);
  cursor: pointer;
}

.spanishExample p, .englishTranslation p {
  font-size: 18px;
  text-align: center;
}

.spanishExample p {
  font-weight: 600;
  font-style: italic;
}

.quizControls {
  height: 240px;
  width:100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: end;
}

.buttonBox {
  text-align: center;
  width: 50%;
  min-width: 300px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  justify-items: stretch;
  margin: 10px auto;
}

.buttonBox button {
  margin: 0px 10px;
  display: block;
  flex-basis: 0;
  flex-grow: 1;
}
.menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menuBox {
  width: 100%;
  align-content: center;
}

.easyButton{
  background-color: darkgreen;
}

.hardButton{
  background-color: darkred;
}

.easyBanner{
  background-color: darkgreen !important;
  cursor: default !important;
}

.hardBanner{
  background-color: darkred !important; 
  cursor: default !important;
}

.progressBar2 {
  display:block;
  text-align: center;
  justify-self: center;
}

@media screen and (max-width: 767px) {
  .div-header {
    flex-direction: column;
    align-items: center;
  }

  .div-header h1 {
    margin-bottom: 20px;
  }

  .spanishExample, .englishTranslation {
    box-sizing: border-box;
    width: 95%;
    min-width: 0;
  }

  .buttonBox {
    width: 95%;
    min-width: 0;
  }
}